<template>
  <el-card class="box-card">
    <el-form ref="dataForm" :model="param" :rules="rule" label-width="80px">
      <el-form-item label="id" prop="id">
        <el-input v-model="param.id" placeholder="id"></el-input>
      </el-form-item>
      <el-form-item label="tag" prop="tag">
        <el-input  type="number" v-model="param.tag" placeholder="tag"></el-input>
      </el-form-item>
      <el-form-item label="fuel" prop="fuel">
        <el-input type="number" v-model="param.fuel" placeholder="fuel"></el-input>
      </el-form-item>
      <el-form-item label="capacity" prop="capacity">
        <el-input type="number" v-model="param.capacity" placeholder="capacity"></el-input>
      </el-form-item>
      <el-form-item label="sales" prop="sales">
        <el-input type="number" v-model="param.sales" placeholder="sales"></el-input>
      </el-form-item>
      <el-form-item label="flownBy" prop="flownBy">
        <el-input v-model="param.flownBy" placeholder="flownBy"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">confirm</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        id: '',
        tag: '',
        fuel: '',
        capacity: '',
        sales: '',
        flownBy: '',
      },
      rule: {
        id: [{required: true, message: "please fill in id", trigger: 'blur'}],
        tag: [{required: true, message: "please fill in tag", trigger: 'blur'}],
        fuel: [{required: true, message: "please fill in fuel", trigger: 'blur'}],
        capacity: [{required: true, message: "please fill in capacity", trigger: 'blur'}],
        sales: [{required: true, message: "please fill in sales", trigger: 'blur'}],
        flownBy: [{required: true, message: "please fill in flownBy", trigger: 'blur'}],
      }
    }
  },
  methods: {
    onSubmit() {
      this.$refs.dataForm.validate((valid) => {
        if (valid) {
          this.$axios.post('/drones/create', this.param).then(res => {
            if (res.success) {
              this.$message({
                message: 'successfully added',
                type: 'success'
              });
              this.$router.back();
            } else {
              this.$message.error(res.message);
            }
          })
        }
      });
    }
  }
}
</script>

